* {
  margin: 0;
  padding: 0;
}

.app {
  text-align: center;
  font-family: 'Montserrat';
  margin-top: 50px;
}

.app .main {
  max-width: 40%;
  margin: 0 auto;
}

.app .main header {
  margin-bottom: 50px;
}
.app .main header img {
  height: 250px;
  margin-bottom: 25px;
}
.app .main header h1 {
  margin-bottom: 25px;
  font-size: 1.6em;
  letter-spacing: 4px;
  text-transform: uppercase;
}

.description {
  text-align: justify;
  margin-bottom: 50px;
}
.description a {
  color: #000;
  font-weight: bold;
}

.skills {
  margin-bottom: 50px;
}
.skills h2 {
  margin-bottom: 25px;
  letter-spacing: 4px;
}
.skills ul {
  padding: 0;
  margin: 0;
  text-align: justify;
  list-style-position: inside;
}

.icons {
  -ms-flex-direction: column;
      flex-direction: column;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 50px;
  position: fixed;
  top: calc(50% - 370px/2);
  right: 5%;
  z-index: 1000;
}
.icons img {
  height: 30px;
  margin: 20px 0;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
}

.icons img:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}

.app footer {
  width: 100%;
  opacity: 0.9;
}

.app footer h2 {
  color: #000;
  letter-spacing: 4px;
}
.app footer .form {
  max-width: 40%;
  margin: 0 auto;
  padding: 20px 0;
}

.app footer .form .block {
  text-align: left;
}
.app footer .form .block input,
.app footer .form .block textarea {
  border: none;
  border-bottom: 1px solid #000; 
  background: none;
  height: 40px;
  width: 100%;
  outline: #000;
  padding-top: 10px;
  font-size: 12px;
}
.app footer .form .block textarea {
  margin-bottom: 50px;
  resize: none;
}

.app footer .form .block #submit {
  background-color: #000;
  border: none;
  color: aliceblue;
  border-radius: 5px;
  font-size: 15px;
  letter-spacing: 4px;
  padding: 0;
  cursor: pointer;
  font-weight: bold;
}

.app footer .form .sucsses {
  color: green;
  text-transform: uppercase;
  -webkit-transition: 2s;
  -o-transition: 2s;
  transition: 2s;
  letter-spacing: 4px;
  padding: 20px;
  height: 40px;
}

.loading {
  height: 100%;
  position: absolute;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.loading img {
  height: 80px;
}

@media all and (max-width: 800px) {
  .app .main {
    max-width: 90%;
  }
  .app footer .form {
    max-width: 90%;
  }
  .icons {
    -ms-flex-direction: row;
        flex-direction: row;
    position: relative;
  }
  .icons img {
    margin: 0 20px;
  }
}


